  font-size: 1.25vw;
  font-style: normal;
  margin-bottom: 1vw;
  font-weight: 600;
  line-height: 100%; /* 1.3451562500000003vw */
  letter-spacing: -0.06723958333333334vw;
}
.gridEm p {
  color: #000;
  font-family: Inter;
  font-size: 1.6vw;
  font-style: normal;
  font-weight: 600;
  line-height: 100%;
  letter-spacing: -0.08854166666666666vw;
  margin-bottom: 1vw;
}
.gridEm p span {
  color: #a8b0b7;
  margin-right: 0.5vw;
}
.gridEm > span {
  color: #a8b0b7;
  font-family: Inter;
  font-size: 0.8vw;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -0.052083333333333336vw;
}
